import React from 'react';
// import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';
import '../scss/About.scss';

export default function About(props) {
  const bodyText = props.bodyText.map((str, i) => { return (<Typography key={i} paragraph={true}>{str}</Typography>); });

  return (
    <div id={"about"} className={"about-cont"}>
      <Typography variant="h2">About</Typography>
      <div className={"content"}>
        <div className={"img"}>
          <img src={props.url} alt={props.name} />
        </div>
        <div className={"basic-info"}>
          <div className={"name-title"}>
            <Typography component="span" className={"name"}>{props.name}</Typography>
            <Typography component="span" className={"title"}>{props.title}</Typography>
          </div>
          <div className={"social-icons"}>
            <Link className={"social linkedin"} href={props.social.linkedin}>
              <LinkedInIcon />
            </Link>
            <Link className={"social github"} href={props.social.github}>
              <GitHubIcon />
            </Link>
          </div>
        </div>
      </div>
      <div className={"overview"}>{bodyText}</div>
    </div>
  )
}